.home {
  display: flex;
  justify-content: space-between;
  margin: 3rem 10rem;
  align-items: center;
  .landing-store {
    border-left: var(--line-color) solid 2px;
    padding: 8rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2,
    h1 {
      font-weight: 200;
      margin-bottom: 1rem;
    }

    h1 {
      letter-spacing: 0.2rem;
      font-size: 40px;
    }
    h2 {
      font-size: 30px;
    }

    button {
      border: none;
      padding: 1rem 2rem;
      border-radius: 0.3rem;
      background-color: #2c8ed6;
      font-weight: bold;
      color: white;
      margin-top: 1.5rem;
    }
    button:hover {
      cursor: pointer;
      background-color: #206596;
    }
  }
}

.slide {
  width: 30vw;
  height: 60vh;
  position: relative;
}

.slide img {
  object-fit: cover;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}
.slider {
  display: flex;
  align-items: center;
  position: relative;

  .fa-angle-right {
    background-color: rgba(255, 255, 255, 0.6);
    padding: 1rem 1.3rem;
    border-radius: 50%;
    z-index: 1;
    left: 85%;
    position: absolute;
    box-shadow: rgba(0, 0, 0, 0.3) -1px 3px 4px;
  }
  .fa-angle-right:hover,
  .fa-angle-left:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.9);
  }
  .fa-angle-left {
    background-color: rgba(255, 255, 255, 0.6);
    padding: 1rem 1.3rem;
    border-radius: 50%;
    z-index: 1;
    right: 85%;
    position: absolute;
    box-shadow: rgba(0, 0, 0, 0.3) -1px 3px 4px;
  }
}
